import { Link } from 'react-router-dom';
import {
    handyDiet,
    bedTime,
    movieSearch,
    calc,
    adopty,
    handydietV2
} from './projectsExports';

const Projects = () => {
    const projects = [
        {
            name: 'bedtime-v2',
            image: bedTime,
            tech: [
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        },
        {
            name: 'Adopty',
            image: adopty,
            tech: [
                { class: 'devicon-vuejs-plain colored mr-1' },
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        },
        {
            name: 'handydiet-v2',
            image: handydietV2,
            tech: [
                { class: 'devicon-react-original colored mr-1' },
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        },
        {
            name: 'HandyDiet',
            image: handyDiet,
            tech: [
                { class: 'devicon-react-original colored mr-1' },
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        },
        {
            name: 'MovieSearch',
            image: movieSearch,
            tech: [
                { class: 'devicon-react-original colored mr-1' },
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        },
        {
            name: 'Calculator',
            image: calc,
            tech: [
                { class: 'devicon-react-original colored mr-1' },
                { class: 'devicon-javascript-plain colored mr-1' },
                { class: 'devicon-html5-plain colored mr-1' },
                { class: 'devicon-css3-plain colored' }
            ]
        }
    ];

    return (
        <div>
            <div className="container text-center mt-2 hero-max-width" style={{ margin: 'auto' }}>
                <h1 className="header-links mx-2" style={{ fontSize: '2.5em' }}>
                    <strong>Projects</strong>
                </h1>
                <p>
                    I've started making tutorials alongside my projects and re-writes. It really
                    allows me to see my growth as a developer. It's something I want to keep pursuing.
                </p>
                <p>
                    As a professional developer who is self-taught I really understand the pain points of finding high quality information, in the massive JavaScript ecosystem, whose content is easy enough to digest.
                </p>
                <p>
                    I'm working on a linear course to address this issue. Incremental steps to help struggling developers be proficient as soon as possible, get out of tutorial hell, and start writing production-ready code.
                </p>
                <p>
                    I'm new to this whole teaching this, but want to get better. Send questions and constructive criticism my way. <a href="mailto:portfolio@zaknichols.com">portfolio@zaknichols.com</a>  
                </p>
            </div>
            <div style={{ width: '75vw', margin: 'auto', display: 'flex', marginBottom: '40px', justifyContent: 'center', flexWrap: 'wrap' }}>
                {projects.map((p, index) => {
                    return (
                        <div key={index} className="card m-3" style={{ width: '18rem' }}>
                            <img className="card-img-top" src={p.image} alt="Project" />
                            <div className="card-body text-center">
                                <h5 className="card-title">{p.name}</h5>
                                {/* <p className="card-text">{p.description}</p> */}
                                <div>
                                    {p.tech.map((t, index) => {
                                        return (
                                            <i key={index} style={{ fontSize: '1.5em' }} className={t.class} />
                                        );
                                    })}
                                </div>
                                <br />
                                <Link to={`/project/${p.name}`} style={{ border: 'none', backgroundColor: '#1e2631' }} className="btn btn-primary">View</Link>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default Projects